<template>
    <div class="forgetWarp">
        <header>
            <router-link to="/home">
                <i class="el-icon-arrow-left"></i>
            </router-link>
            <div>重置密码</div>
        </header>
        <form action="" class="restForm">
            <section class="form_section1">
                <input type="text" placeholder="账号" v-model="username">
            </section>
            <section class="form_section1">
                <input type="text" placeholder="旧密码" v-model="old_passWord">
            </section>
            <section class="form_section1">
                <input type="text" placeholder="请输入新密码" v-model="new_password">
            </section>
            <section class="form_section1">
                <input type="text" placeholder="请确认新密码" v-model="con_firmpassword">
            </section>
            <section class="form_section2">
                <input type="text" placeholder="验证码" v-model="captcha_code">
                <div>
                    <img :src="capUrl" alt="">
                    <div @click="changeCap">
                        <p>看不清</p>
                        <p>换一张</p>
                    </div>
                </div>
            </section>
            <div class="login_container" @click="gaiAction()">确认修改</div>
        </form>

        <section class="tip_text_container" v-if="isShow">
            <div class="tip_icon">
                <span class="s1"></span>
                <span class="s2"></span>
            </div>
            <p class="tip_text">密码修改成功</p>
            <div class="confrim" @click="queBtn1">确认</div>
        </section>
        <section class="tip_text_container" v-if="isShow1">
            <div class="tip_icon">
                <span class="s1"></span>
                <span class="s2"></span>
            </div>
            <p class="tip_text">密码不正确</p>
            <div class="confrim" @click="queBtn2">确认</div>
        </section>
        <section class="tip_text_container" v-if="isShow2">
            <div class="tip_icon">
                <span class="s1"></span>
                <span class="s2"></span>
            </div>
            <p class="tip_text">两次密码不一致</p>
            <div class="confrim" @click="queBtn3">确认</div>
        </section>
        <section class="tip_text_container" v-if="isShow3">
            <div class="tip_icon">
                <span class="s1"></span>
                <span class="s2"></span>
            </div>
            <p class="tip_text">验证码不正确</p>
            <div class="confrim" @click="queBtn4">确认</div>
        </section>
    </div>
</template>

<script>
    export default {
        name: "Forget",
        data(){
            return {
                username:'',
                old_passWord:'',//旧密码
                password:'',
                new_password:'',
                con_firmpassword:'',
                captcha_code: "",
                capUrl:"",
                isShow:false,
                isShow1:false,
                isShow2:false,
                isShow3:false,
            }
        },
        created(){
           this.requestCode()
        },
        methods:{
            gaiAction(){
                console.log(this.username,this.old_passWord,this.new_password,this.con_firmpassword)
                this.axios.post("/v2/changepassword",{
                     username:this.username,
                    oldpassWord:this.old_passWord,
                    newpassword:this.new_password,
                    confirmpassword:this.con_firmpassword,
                    captcha_code:this.captcha_code
                }).then(res=>{
                      console.log(res);
                    if (res.success == '密码修改成功') {
                        this.isShow = true;
                    }else if (res.message == '密码不正确') {
                        this.isShow1 = true;
                    }else if (res.message == '两次密码不一致') {
                        this.isShow2 = true;
                    }else{
                        this.isShow3 = true;
                    }

                }).catch(err=>{})
            },
            //封装一个方法  请求验证码的方法  在网页进入的时候请求  还需要在换一张的时候请求
            requestCode(){
                this.axios.post("/v1/captchas").then(res=>{
                    // console.log(res);
                    this.capUrl = res.code;
                }).catch(err=>{});
            },
            changeCap(){
                this.requestCode();
            },
            queBtn1(){
                this.isShow = false;
            },
            queBtn2(){
                this.isShow1 = false;
            },
            queBtn3(){
                this.isShow2 = false;
            },
            queBtn4(){
                this.isShow3 = false;
            },
        }
    }
</script>

<style scoped>
    .forgetWarp {
        padding-top: 1.95rem;
    }
    header {
        background-color: #3190e8;
        position: fixed;
        z-index: 100;
        left: 0;
        top: 0;
        width: 100%;
        height: 1.95rem;
    }
    header>div {
        width: 50%;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
        color: #fff;
        text-align: center;
        font-size: .8rem;
        font-weight:700;
    }
    header>a {
        position: absolute;
        top: 50%;
        left: .4rem;
        transform: translateY(-50%);
        color: #fff;
    }
    
    .restForm{
        background-color: #fff;
        margin-top: .6rem;
        display: block;
    }
    .form_section1{
        height: 1.5rem;
        display: flex;
        justify-content: space-between;
        border-bottom: 1px solid #f1f1f1;
        padding: .3rem .8rem;
    }
    .form_section1 input{
        border: none;
        outline: none;
        font-size: .7rem;
        color: #666;
    }
    .form_section2{
        /*height: 1.5rem;*/
        display: flex;
        justify-content: space-between;
        padding: .6rem .8rem;
        border-bottom: 1px solid #f1f1f1;
    }
    .form_section2 input {
        font-size: .7rem;
        color: #666;
        border: none;
        outline: none;
    }
    .form_section2>div {
        display: flex;
        align-items: center;
    }
    .form_section2>div p{
        font-size: .55rem;
        color: #666;
    }
    .form_section2>div p+p{
        color: #3b95e9;
        margin-top: .2rem;
    }
    .form_section2>div img{
        width: 3.5rem;
        height:1.2rem;
        margin-right: .2rem;
    }

    .login_container{
        margin: 1rem .5rem;
        font-size: .7rem;
        color: #fff;
        background-color: #4cd964;
        padding: .5rem 0;
        border: 1px;
        border-radius: .15rem;
        text-align: center;
    }

    .tip_text_container{
        position: absolute;
        top: 50%;
        left: 50%;
        margin-top: -6rem;
        margin-left: -6rem;
        width: 12rem;
        animation: tipMove .4s;
        background-color: #fff;
        padding-top: .6rem;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        border: 1px;
        border-radius: .25rem;
    }
    .confrim{
        font-size: .8rem;
        color: #fff;
        font-weight: 700;
        margin-top: .8rem;
        background-color: #4cd964;
        width: 100%;
        text-align: center;
        line-height: 1.8rem;
        border: 1px;
        border-bottom-left-radius: .25rem;
        border-bottom-right-radius: .25rem;
    }
    .tip_text{
        font-size: .7rem;
        color: #333;
        line-height: .9rem;
        text-align: center;
        margin-top: .8rem;
        padding: 0 .4rem;
    }
    .tip_icon{
        width: 3rem;
        height: 3rem;
        border: .15rem solid #f8cb86;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
    .s1{
        width: .12rem;
        height: 1.5rem;
        background-color: #f8cb86;
    }
    .s2{
        width: .2rem;
        height: .2rem;
        border: 1px;
        border-radius: 50%;
        margin-top: .2rem;
        background-color: #f8cb86;
    }

</style>